@import "_config/scss/vars"; @import "_config/scss/common";



// Common vars
$zMainActions: 1500;
$zMobileActionToggle: 1500;
$zOverlay: 1400;
$zDetail: 1600;

$zPickerMessage: 1500;
$xPickerCancel: 1600;


// Mixins
@import 'mixins/abs-fill';
@import 'mixins/fill-parent';
@import 'mixins/primary-bottom';

// Components
@import 'components/SurveyComponent';
@import 'components/SurveyResponseComponent';
@import 'components/LoadingComponent';
@import 'components/VoteSectionComponent';
@import 'components/CommentSectionComponent';
@import 'components/FullOverlay';

// States
@import 'components/DetailMapState';
@import 'components/DefaultMapState';
@import 'components/PickingMapState';

// Pages
@import 'pages/MapPage';

// Transitions
@import 'transitions/fade';
@import 'transitions/fade-up';
@import 'transitions/grow-fade';
@import 'transitions/fade';
@import 'transitions/component-switch';


@keyframes media-grow {
  from { transform: scale(0.5); opacity: 0; }
  to   { transform: scale(1.0); opactity: 1; }
}

.survey-response .media {
  animation: media-grow;
}



